.original {
    @include justifyCenterCol;
    position: relative;
    overflow: hidden;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 70%, rgba(0, 0, 0, 0)100%);

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    
}

.new {
    position: absolute;
    top:0;
    right: 0;
    left: 0;
    z-index: -1;
    padding:5%;
}

.new-ipad {
    @include mq(sm){
        padding: 2.5%;
    }
    padding: 5%;
}

.image-parent {
    position: relative;
    overflow: hidden;
}

.emoji {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    height: rem($circle-inside-emoji-dimension);
    width: rem($circle-inside-emoji-dimension);
    z-index: -1;
}

.image {
    height: 100%;
    width: 100%;
    object-fit: cover;
}